
#translateForm {
  padding: 0 15px;
}
#translateForm .langRadioGroup {
    display: inline-block;
}
@media (max-width: 350px) {
#translateForm .langRadioGroup {
        width: 75%;
}
}
#translateForm .langRadioGroup .langRadioBtn {
      position: relative;
      margin: 0 2.5px;
}
#translateForm .langRadioGroup .langRadioBtn:first-child {
        margin-left: 0;
}
@media (max-width: 350px) {
#translateForm .langRadioGroup .langRadioBtn.mostPopLang {
          display: block;
          margin: 10px;
}
}
#translateForm .langRadioGroup .langRadioBtn input {
        position: absolute;
        top: 0;
        left: 0;
        bottom: 0;
        width: 100%;
        margin: 0;
        padding: .5em;
        border-radius: 3px;
}
#translateForm .langRadioGroup .langRadioBtn input:before, #translateForm .langRadioGroup .langRadioBtn input:after {
          border: none;
          color: transparent;
          background: transparent;
}
#translateForm .langRadioGroup .langRadioBtn input:checked {
          background: #4caf50;
}
#translateForm .langRadioGroup .langRadioBtn input:checked + label {
          color: white;
}
#translateForm .langRadioGroup .langRadioBtn label {
        position: relative;
        z-index: 2;
        margin: 0;
        font-weight: bold;
        cursor: pointer;
}
#translateForm .langRadioGroup .toggle-group {
      position: static;
      margin: 0 5px 0 2.5px;
}
#translateForm .langRadioGroup .toggle-group .dropdown-toggle {
        background: white;
}
#translateForm .langRadioGroup .toggle-group .dropdown-toggle:hover {
          background: #efefef;
}
#translateForm .langRadioGroup .toggle-group .toggle-display {
        position: absolute;
        z-index: 2;
        left: 0;
        right: 0;
        margin: 50px 15px;
        border-radius: 4px;
        background: #efefef;
        box-shadow: 0 2px 4px gray;
        column-count: 5;
        column-gap: 0;
        border-left: 1px solid lightgray;
}
@media (max-width: 600px) {
#translateForm .langRadioGroup .toggle-group .toggle-display {
            column-count: 3;
}
}
#translateForm .langRadioGroup .toggle-group .toggle-display .toggleBtns {
          background: white;
          border-bottom: 1px solid lightgray;
          border-right: 1px solid lightgray;
          margin: 0;
          transition: all 0.4s;
}
#translateForm .langRadioGroup .toggle-group .toggle-display .toggleBtns:hover {
            background: #efefef;
}
#translateForm .langRadioGroup .toggle-group .toggle-display .toggleBtns input {
            border-radius: 0;
}
@media (max-width: 960px) {
#translateForm input[type="submit"] {
      display: block;
      margin: 15px auto 0 auto;
}
}
#translateForm .translateInput {
    width: 48.5%;
    margin: 15px 15px 15px 0;
    position: relative;
}
@media (max-width: 960px) {
#translateForm .translateInput {
        width: 100%;
        margin: 15px 0;
}
}
#translateForm .translateInput textarea {
      background: white;
      width: 100%;
      padding: 25px 10px 10px;
      box-shadow: 0 4px 8px lightgray;
      resize: none;
      border-radius: 3px;
      min-height: 200px;
      outline: none;
}



